
import { mapState } from "vuex";
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'

export default {
name: "topiclist",
props: ["name"],
components: {
Group,
Cell,
Scroller,
Tab,
TabItem
},
data() {
return {
lists: [],

page: 1,

limit: 15,

pullupConfig: {
content: "上拉加载",
downContent: "松开加载",
upContent: "上拉加载",
loadingContent: "加载中"
},

pulldownConfig: {
content: "下拉刷新",
downContent: "松开刷新",
upContent: "下拉刷新",
loadingContent: "加载中"
}
};
},
computed: {
...mapState(["topics"])
},
methods: {

freshData() {
var app = this;
this.$http
.get(
"/topics",
{
params: {
tab: this.name,
page: 1,
limit: this.limit
}
}
)
.then(res => {
if (res.data && res.data.success) {
app.lists = res.data.data;

app.$refs.pullup.donePulldown();
}
});
},

getDescByTab(tab) {
return this.$store.getters.getTopicDescByTab(tab);
},

routeto(index) {
this.$router.push("/topiclist/" + this.topics[index].name);
},

loadMoreData() {

this.page += 1;
this.$http
.get("/topics", {
params: {
tab: this.name,
page: this.page,
limit: this.limit
}
})
.then(res => {
if (res.data.success) {

for (var i = 0; i < res.data.data.length; i++) {
this.lists.push(res.data.data[i]);
}


this.$refs.pullup.donePullup();
}
});
}
},


beforeRouteEnter(to, from, next) {






next(function(vm) {

vm.$http
.get("/topics", {
params: {
tab: to.params.name,
page: vm.page,
limit: vm.limit
}
})
.then(res => {


if (res.data.success) {
vm.lists = res.data.data;
}
});
});
},

beforeRouteUpdate(to, from, next) {

var app = this;


this.$http
.get("/topics", {
params: {


tab: to.params.name,
page: app.page,
limit: app.limit
}
})
.then(res => {
if (res.data.success) {

app.lists = res.data.data;

}
});

next();
}
};



