
import Card from 'vux/src/components/card/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'

export default {
name: "user",
props: ["loginname"],
data() {
return {
userinfo: [],
collect_topics: []
};
},
components: {
Card,
Group,
Cell
},
beforeRouteEnter(to, from, next) {
next(vm => {

vm.$http.get("/user/" + to.params.loginname).then(res => {
vm.userinfo = res.data.data;
});

vm.$http.get("/topic_collect/" + to.params.loginname).then(res => {
vm.collect_topics = res.data.data;
});
});
},
beforeRouteUpdate(to, from, next) {
var app = this;
this.$http.get("/user/" + to.params.loginname).then(res => {
app.userinfo = res.data.data;
});
next();
}
};



