
import XHeader from 'vux/src/components/x-header/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import Badge from 'vux/src/components/badge/index.vue'

import { mapState, mapGetters, mapMutations } from "vuex";
import VmBackTop from "vue-multiple-back-top";
export default {
name: "app",
data() {
return {
badgeText: ""
};
},
components: {
XHeader,
Actionsheet,
VmBackTop,
Badge
},
computed: {

...mapGetters(["moreMenus"]),

...mapState([
"topics",
"loginedMoreMenus",
"unLoginedMoreMenus",
"isLogined"
]),
isShowMoreMenus: {

get() {
return this.$store.state.isShowMoreMenus;
},

set(val) {

return this.$store.commit("mutationIsShowMoreMenus", val);
}
}
},
methods: {
...mapMutations([
"mutationLogin",
"mutationLoginname",
"mutationAccessToken"
]),
getUnreadMsgCount() {
var app = this;
this.$http
.get("/message/count", {
params: {
accesstoken: this.$store.state.accesstoken
}
})
.then(res => {

if (res.data.success) {
app.badgeText = res.data.data;
}
});
},

routetohome() {
this.$router.replace("/");
},

showMoreMenus() {


this.isShowMoreMenus = true;
},

handleClickMoreMenu(menukey, menuitem) {

this.$router.push("/" + menukey);
}
},
created() {

var loginStatus = window.localStorage.getItem("loginStatus");
var loginname = window.localStorage.getItem("loginname");
var accesstoken = window.localStorage.getItem("accesstoken");

if (loginStatus) {

this.mutationLogin();
this.mutationLoginname(loginname);
this.mutationAccessToken(accesstoken);
}
var app = this;
app.getUnreadMsgCount();
window.setInterval(() => {
app.getUnreadMsgCount();
}, 5 * 60 * 1000);
}
};



